import React from "react"
import { StaticQuery, graphql } from "gatsby"

import TitleAndDesc from "./TitleAndDesc"

export default props => {
  return (
    <StaticQuery
      query={graphql`
        query {
          site {
            siteMetadata {
              title
              description
            }
          }
        }
      `}
      render={data => <TitleAndDesc data={data} />}
    />
  )
}
